import React, {useState} from 'react';
import style from "../contact/contact.module.sass";
import useFetch from "../../hooks/useFetch";

function Admin() {
  const [{response, isLoading}, doFetch] = useFetch("https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAUkPoluxh14m21DEHDSCVxDmgMf5Dahcc");
  const [form, setForm] = useState({});

  function onChangeForm(e) {
    setForm({...form, returnSecureToken: true,  [e.target.name]: e.target.value});
  }

  function sendForm() {
    doFetch({
      method: "post",
      data: form
    });
  }

  return (
    <div>
      <h3 className="title-text">Admin</h3>
      <div className={style.form}>
        <div className="row">
          <div className="input-field col s12">
            <input onChange={onChangeForm} id="email" name="email" type="email"/>
            <label htmlFor="email">Email</label>
          </div>
        </div>
        <div className="row">
          <div className="input-field col s12">
            <input onChange={onChangeForm} id="password" name="password" type="password"/>
            <label htmlFor="password">Email</label>
          </div>
        </div>
        <button disabled={isLoading} onClick={sendForm} className="btn waves-effect waves-light" name="action">
          Login <i className="fas fa-paper-plane"/>
        </button>
      </div>
    </div>
  );
}

export default Admin;
